import React from "react";
import NavBar from "../components/Navbar";

export default function Schemes() {
  return (
    <div className="min-h-screen bg-gray-50">
      <NavBar />
      <div className="max-w-6xl mx-auto py-12 px-6">
        <h2 className="text-3xl font-bold text-blue-700 mb-6">Government Schemes</h2>
        <div className="grid md:grid-cols-3 gap-6">
          <div className="bg-white rounded-xl shadow-md overflow-hidden">
            <img
              src="/images/scheme1.jpg"
              alt="Scheme 1"
              className="w-full h-40 object-cover"
            />
            <div className="p-4">
              <h3 className="text-xl font-semibold mb-2">PM Awas Yojana</h3>
              <p className="text-sm text-gray-600">
                Affordable housing scheme for urban and rural poor with subsidies on home loans.
              </p>
            </div>
          </div>

          <div className="bg-white rounded-xl shadow-md overflow-hidden">
            <img
              src="/images/scheme2.jpg"
              alt="Scheme 2"
              className="w-full h-40 object-cover"
            />
            <div className="p-4">
              <h3 className="text-xl font-semibold mb-2">Ayushman Bharat</h3>
              <p className="text-sm text-gray-600">
                Health insurance coverage of up to 5 lakh per family for secondary and tertiary care.
              </p>
            </div>
          </div>

          <div className="bg-white rounded-xl shadow-md overflow-hidden">
            <img
              src="/images/scheme3.jpg"
              alt="Scheme 3"
              className="w-full h-40 object-cover"
            />
            <div className="p-4">
              <h3 className="text-xl font-semibold mb-2">Skill India</h3>
              <p className="text-sm text-gray-600">
                Initiative to train over 40 crore people in different skills by 2025.
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
